.big-button {
  height: 50px;
  align-items: center;
  color: white;
  align-items: center;
}

.big-button.primary {
  background-color: #7a6fbe;
  background-color: var(--btn-primary-color);
}

.big-button.info {
  background-color: #08a7c3;
  background-color: var(--btn-info-color);
}

.big-button.success {
  background-color: #22baa0;
  background-color: var(--btn-success-color);
}

.big-button.danger {
  background-color: #f25656;
  background-color: var(--btn-danger-color);
}

.button-container > button {
  margin-right: 2px;
  margin-bottom: 5px;
}

.button-container > button:last-child {
  margin-right: 0px;
}
.two-icon-button-container {
  align-items: center;
  margin: auto !important;
  padding: 20px;
  height: 55px;
  margin-bottom: 10px;
}

.two-icon-button-container > .icon-container {
  width: 20px;
}

.two-icon-button-container:hover {
  cursor: pointer;
}
.two-icon-button-container > .button-title {
  margin: 0px 12px;
  font-size: 14px;
  font-weight: bold;
}
/* Custom Button ->end */
