.react-select-bulk__control {
  border: 1px solid #dce1e4 !important;
  box-shadow: none !important;
}

.react-select-bulk__input input {
  margin-bottom: 0px;
}

.react-select-bulk__clear-indicator svg {
  height: 13px;
  color: #4e5e6a;
  color: var(--main-text-color);
}

.react-select-bulk__multi-value__remove {
  padding-top: 2px !important;
}

.react-select-bulk__multi-value__remove svg {
  height: 13px;
}

.react-select-bulk__multi-value__label {
  color: white !important;
  font-family: 'Open Sans', sans-serif !important;
  font-size: 13px !important;
  padding: 2px 0px 2px 5px !important;
}

.react-select-bulk__multi-value {
  background-color: #7a6fbe !important;
  background-color: var(--btn-primary-color) !important;
  color: white !important;
  white-space: nowrap;
  list-style: none;
  box-sizing: border-box;
  border-radius: 5px !important;
  height: 22px;
}

.react-select-bulk__indicator-separator,
.react-select-bulk__dropdown-indicator {
  display: none !important;
}

.react-select__input > input {
  margin-bottom: 0px;
}

.react-select__value-container {
  padding-top: 0px !important;
}
